.form{
    padding:10px;
    display:flex;
    align-items: center;
    justify-content: center;
    
  }
  
.input{
    border:1px solid rgb(173, 145, 145);
    border-radius:5px;
    width:60%;
    padding:10px;
    border-color: none;
}
.btn{
    padding:10px;
    width:7%;
    background-color: #1b3987;
    background-color: green;
    color:white;
    margin-left:20px;
    font-size:16px;
    border:none;
    cursor: pointer;
  }

  @media(max-width:700px){
    .input{
      width:90%;
      margin:auto;
      padding:10px;
    }
    .btn{
      margin:auto;
      margin-top:10px;
      width:15%;
      padding:10px;
    }
  }