.url{
    display:flex;
    align-items: center;
    justify-content: space-around;
    padding:10px;
    width:70%;
    margin:auto;
    margin-top:20px;
    background-color: white;
    border-radius: 2px;
  }
  

.longid{
    text-decoration: none;
    font-family:Arial;
    font-size:18px;
    color:rgb(41, 26, 26);
  }
  
.shortid{
    text-decoration:none;
    font-size:18px;
  }
  
.btn-2{
    background-color: #eee;
    color:blue;
    border:none;
    padding:7px;
    border:none;
    cursor: pointer;
  }

  @media(max-width:700px)
  {
    .url{
      width:90%;
    }
  }